import { SET_WEATHER_FOR_1_DAY, SET_WEATHER_FOR_5_DAYS } from '../../utils/constants';

const initialState = {
    weatherFor1Day: undefined,
    weatherFor5Days: undefined
};

function WeatherReducer (state = initialState, action) {
    switch (action.type) {
        case SET_WEATHER_FOR_1_DAY:
            return {...state, weatherFor1Day: action.payload};

        case SET_WEATHER_FOR_5_DAYS:
            return {...state, weatherFor5Days: action.payload};

        default:
            return state;
    }
}

export default WeatherReducer;


