import React, {Component} from 'react';
import {
    getUrlIcons,
    kelvinToCesium,
    hPaTommHg,
    degDirection,
    parseTime
} from '../../utils/secondaryFunctions';
import './weatherByHours.css'

class WeatherByHours extends Component {
    render() {
        return (
            <div className="weather-by-hours">
                {this.props.weather.map((item, index) => {
                    const humidity = item.main.humidity;
                    const temp = kelvinToCesium(item.main.temp);
                    const pressure = hPaTommHg(item.main.pressure);
                    const icon = getUrlIcons(item.weather[0].icon);
                    const windDirection = degDirection(item.wind.deg);
                    const windSpeed = item.wind.speed;
                    const time = parseTime(item.dt_txt);

                    console.log();
                    
                    return (
                        <div key={index} className="weather-by-hours__item">
                            <div className="weather-by-hours__time">{time}</div>
                            <div className="weather-by-hours__ico"><img src={icon} alt="icon"/></div>
                            <div className="weather-by-hours__temp">{temp}</div>
                            <div className="weather-by-hours__pressure">{pressure}</div>
                            <div className="weather-by-hours__humidity">{humidity}</div>
                            <div className="weather-by-hours__wind">
                                <i className={"fa fa-long-arrow-up " + windDirection} aria-hidden="true"/>
                                <div className="wind__speed">{windSpeed}</div>
                            </div>
                        </div>
                    );
                })}
            </div>
        );
    }
}

export default WeatherByHours;


