import { SET_CITY } from '../../utils/constants';

const initialState = {
  city: ''
};

function locationReducer (state = initialState, action) {
    switch (action.type) {
        case SET_CITY:
            return {...state, city: action.payload};

        default:
            return state;
    }
}

export default locationReducer;


