import {
    URL_WEATHER_FOR_1_DAY,
    URL_WEATHER_FOR_5_DAYS,
    OPEN_WEATHER_KEY,
    URL_WEATHER_ICONS
} from './constants';

export function getUrlWeatherFor1Day(city) {
    return URL_WEATHER_FOR_1_DAY + city + '&APPID=' + OPEN_WEATHER_KEY;
}

export function getUrlWeatherFor5Days(city) {
    return URL_WEATHER_FOR_5_DAYS + city + '&APPID=' + OPEN_WEATHER_KEY;
}

export function getUrlIcons(icon) {
    return URL_WEATHER_ICONS + icon + '.png'
}

export function kelvinToCesium(kelvin) {
    // eslint-disable-next-line
    return parseInt(kelvin - 273.15);
}

export function hPaTommHg(hPa) {
    // eslint-disable-next-line
    return parseInt(hPa / 1.33322368421)
}

export function degDirection(deg) {
    if (deg > 340 && deg <= 20) return 'S'; // N
    if (deg > 20 && deg <= 70) return 'SW'; // NE
    if (deg > 70 && deg <= 115) return 'W'; // E
    if (deg > 115 && deg <= 155) return 'NW'; //SE
    if (deg > 155 && deg <= 200) return 'N'; //S
    if (deg > 200 && deg <= 245) return 'NE'; //SW
    if (deg > 245 && deg <= 295) return 'E'; //W
    if (deg > 295 && deg <= 340) return 'SE'; //NW
}

export function getWeatherFor1Day(location, callback) {
    return fetch(getUrlWeatherFor1Day(location))
        .then(response => response.text())
        .then(json => callback(JSON.parse(json)))
        .catch((err) => {
            console.log(err);
        });
}

export function getWeatherFor5Days(location, callback) {
    return fetch(getUrlWeatherFor5Days(location))
        .then(response => response.text())
        .then(json => callback(JSON.parse(json).list))
        .catch((err) => {
            console.log(err);
        });
}

export function parseTime (time) {
    let time1 = time.split(' ')[1].split(':')[0];
    let time2 = time.split(' ')[1].split(':')[1];

    return time1 + ':' + time2;
}

export function formatTime (options, time) {
    const timeFormat = new Intl.DateTimeFormat("ru-RUS", options).format;
    return timeFormat(time);
}


