import { SET_WEATHER_FOR_1_DAY, SET_WEATHER_FOR_5_DAYS } from '../../utils/constants';

export function setWeatherFor1Day (weather) {
    return {type: SET_WEATHER_FOR_1_DAY, payload: weather}
}

export function setWeatherFor5Days (weather) {
    return {type: SET_WEATHER_FOR_5_DAYS, payload: weather}
}


