import React, {Component} from 'react';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import * as locationActions from './store/actions/LocationActions';
import * as weatherActions from './store/actions/WeatherActions';
import Header from './components/header/Header';
import LocationForm from './components/location/LocationForm';
import Tabs from './components/tabs/Tabs';
import WeatherFor1Day from './components/weather/WeatherFor1Day';

class App extends Component {
    render() {
        const { setCity } = this.props.locationAction;
        const location = this.props.location;
        const { setWeatherFor1Day, setWeatherFor5Days } = this.props.weatherActions;
        const {weatherFor1Day ,weatherFor5Days } = this.props.weather;
        
        return (
            <div className="App">
                <Header/>
                <LocationForm
                    setCity={setCity}
                    setWeatherFor1Day={setWeatherFor1Day}
                    setWeatherFor5Days={setWeatherFor5Days}
                    location={location}
                />
                <Tabs
                    titles={['One day', 'Five days']}
                >
                    <WeatherFor1Day
                        weatherFor5Days={weatherFor5Days}
                        weatherFor1Day={weatherFor1Day}
                        location={location}
                    />
                    <div>Lorem ipsum dolor sit amet, consectetur adipisicing elit. Aliquid animi aperiam cumque
                        dolorem dolores et eveniet ex exercitationem iusto magni nemo, praesentium quae quo quos,
                        ratione soluta voluptate! A alias eaque enim facilis fuga minima minus rem rerum sint unde.
                    </div>
                </Tabs>
            </div>
        );
    }
}

function mapStateToProps(state) {
    return {
        location: state.location.city,
        weather: state.weather
    }
}

function mapDispatchToProps(dispatch) {
    return {
        locationAction: bindActionCreators(locationActions, dispatch),
        weatherActions: bindActionCreators(weatherActions, dispatch)
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(App);



