import React, {Component} from 'react';
import Autocomplete from 'react-google-autocomplete';
import './location-form.css';
import {getWeatherFor1Day, getWeatherFor5Days} from '../../utils/secondaryFunctions';

class LocationForm extends Component {
    constructor(props) {
        super(props);

        getWeatherFor1Day(
            this.props.location,
            this.props.setWeatherFor1Day
        );

        getWeatherFor5Days(
            this.props.location,
            this.props.setWeatherFor5Days
        );

        this.handleAutocompleteSelected = this.handleAutocompleteSelected.bind(this);
    }

    handleAutocompleteSelected(place) {
        const city = place.address_components[0].long_name;
        this.props.setCity(city);

        getWeatherFor1Day(
            city,
            this.props.setWeatherFor1Day
        );

        getWeatherFor5Days(
            city,
            this.props.setWeatherFor5Days
        )
    }

    render() {
        return (
            <form id="location-form">
                <Autocomplete
                    className="autocomplete"
                    placeholder="Select city"
                    onPlaceSelected={this.handleAutocompleteSelected}
                    types={['(regions)']}
                />
            </form>
        );
    }
}

export default LocationForm;


