import React, {Component} from 'react';
import './tabs.css';

class Tabs extends Component {
    constructor(props) {
        super(props);

        this.state = {
            active: 0
        };

        this.handleClick = this.handleClick.bind(this);
    }

    handleClick(event) {
        this.setState({
            active: event.target.dataset.count
        });

        return false;
    }

    render() {
        return (
            <div className="tabs">
                <ul className="tabs__nav">
                    {this.props.titles.map((item, index) => {
                        return (
                            // eslint-disable-next-line
                            <a
                                key={index}
                                href="#"
                                onClick={this.handleClick}
                                data-count={index}
                                // eslint-disable-next-line
                                className={this.state.active == index ? 'tabs__nav-item tabs__nav-item_active' : 'tabs__nav-item'}
                            >{item}</a>
                        );
                    })}
                </ul>
                <ul className="tabs__content">
                    {this.props.children.map((item, index) => {
                        return (
                            <div
                                key={index}
                                // eslint-disable-next-line
                                className={this.state.active == index ? 'tabs__content-item tabs__content-item_active' : 'tabs__content-item'}
                            >{item}</div>
                        );
                    })}
                </ul>
            </div>
        );
    }
}

export default Tabs;


