import React, {Component} from 'react';
import {
    getUrlIcons,
    kelvinToCesium,
    hPaTommHg,
    degDirection,
    formatTime
} from '../../utils/secondaryFunctions';
import './weatherCard.css'

class WeatherCard extends Component {
    render() {
        const humidity = this.props.weather.main.humidity;
        const temp = kelvinToCesium(this.props.weather.main.temp);
        const sunrise = formatTime(
            {hour: "2-digit", minute: "2-digit"},
            this.props.weather.sys.sunrise + '000'
        );
        const sunset = formatTime(
            {hour: "2-digit", minute: "2-digit"},
            this.props.weather.sys.sunset + '000'
        );
        const pressure = hPaTommHg(this.props.weather.main.pressure);
        const description = this.props.weather.weather[0].description;
        const icon = getUrlIcons(this.props.weather.weather[0].icon);
        const windDirection = degDirection(this.props.weather.wind.deg);
        const windSpeed = this.props.weather.wind.speed;

        return (
            <div className="weather-card">
                <div className="weather-card__left">
                    <div className="weather-card__ico"><img src={icon} alt="ico"/></div>
                    <div className="weather-card__temperature">{temp}</div>
                    <div className="weather-card__desc">{description}</div>
                </div>
                <div className="weather-card__right">
                    <div className="wind">
                        <i className={"fa fa-long-arrow-up " + windDirection} aria-hidden="true"/>
                        <div className="wind__speed">{windSpeed}</div>
                        <div className="wind__deg">{windDirection}</div>
                    </div>
                    <div className="pressure">{pressure}</div>
                    <div className="humidity">{humidity}</div>
                    <div className="weather-card__time">
                        <div className="sunrise">Sunrise - {sunrise}</div>
                        <div className="sunset">Sunset - {sunset}</div>
                    </div>
                </div>
            </div>
        );
    }
}

export default WeatherCard;


